import React, { Component } from 'react'
import { connect } from 'react-redux'
import styled from 'styled-components';
import Reitg from './Reitg';
import Clock from './Clock';
import Crew from './Crew';

const HR = styled.hr`
  margin: 1px;
`;
const H2 = styled.h2`
  color: white;
  float: left;
  margin-left: 50px;
  margin-top: 8px;
`;
const H3 = styled.h3`
  color: white;
  margin: 1px;
  float: left;
  width: 100%;
`;
const SCRN = styled.div`
  background: url(http://kshisa.ru/images/butt/TV01.png) no-repeat;
  background-size: contain;
  height: 316px;
`;
const POST = styled.img`
  float: left;
  width: 170px;
  margin-left: 17px;
  margin-top: 15px;
`;
const FOTO = styled.div`
  float: left;
  width: 360px;
  margin-top: 15px;
  margin-left: 6px;
`;
const POLR = styled.div`
  position: relative;
  width: 90%;
  margin: auto;
`;
const INER = styled.img`
  border: 10px solid #fff;
  -webkit-box-shadow: 3px 3px 3px #777;
  -moz-box-shadow: 3px 3px 3px #777;
  box-shadow: 3px 3px 3px #777;
  width:70px;
`;
const IMG = styled.img`
  width: 170px;
  margin-left: 10px;
`;
const BILL = styled.img`
  width: 58px;
  margin-left: 4px;
  margin-top: 7px;
  margin-bottom: 4px;
`;
const BUTT = styled.img`
  margin-left: 10px;
`;
class Card extends Component {
  constructor(props) {
    super(props)
    this.state = {ButNumbl: 0,
                  ButNumbr: 0}
    this.buttClickc = this.buttClickc.bind(this);
    this.buttClickg = this.buttClickg.bind(this);
    this.buttClickr = this.buttClickr.bind(this);
    this.buttClickd = this.buttClickd.bind(this);
  }

  buttClickc = () => {
    this.setState(state => ({
      ButNumbl: 0
    }));
  }
  buttClickg = () => {
    this.setState(state => ({
      ButNumbl: 1
    }))
  }
  buttClickr = () => {
    this.setState(state => ({
      ButNumbr: 0
    })) 
  }
  buttClickd = () => {
    this.setState(state => ({
      ButNumbr: 1
    }))   
  }
  
	render () {
    const { info } = this.props
    const { butsc } = this.props
    const { butsg } = this.props
    let butsl
    let butsr
    if (this.state.ButNumbl == 0) {
      butsl = butsc
    }
    else {
      butsl = butsg
    }
    if (this.state.ButNumbr == 0) {
      butsr = <Reitg />
    }
    else {
      butsr = <Clock />
    }
		return (
		  <div>
		    <SCRN>
			    <POST
            src={'http://kshisa.ru/images/mini/' + info[0] +  'm0.jpg'}
            alt=''
          />
			    <FOTO>
			    <IMG
            src={'http://kshisa.ru/images/mini/' + info[0] +  'm1.jpg'}
            alt=''
          />
			    <IMG
            src={'http://kshisa.ru/images/mini/' + info[0] +  'm2.jpg'}
            alt=''
          />
			    <IMG
            src={'http://kshisa.ru/images/mini/' + info[0] +  'm3.jpg'}
            alt=''
          />
			    <IMG
            src={'http://kshisa.ru/images/mini/' + info[0] +  'm4.jpg'}
            alt=''
          />
        </FOTO>
        <H3>{ info[2] }</H3>
        <H3>{ info[1]+'  ('+info[4]+')' }</H3>        
			</SCRN>
      <HR></HR> 
      <H3>
        {butsl.map(but => (
            <BILL
              src={'http://kshisa.ru/images/bill/'+ but +'.png'}
              alt=''
            />
        ))}
     {butsr}
      </H3>
          <HR></HR> 
          <BUTT
            src={'http://kshisa.ru/images/bill/butsc.png'}
            alt=''
            onClick={() => this.buttClickc()}
          />
          <BUTT
            src={'http://kshisa.ru/images/bill/butsg.png'}
            alt=''
            onClick={() => this.buttClickg()}
          />
          <BUTT
            src={'http://kshisa.ru/images/bill/butsr.png'}
            alt=''
            onClick={() => this.buttClickr()}
          />
          <BUTT
            src={'http://kshisa.ru/images/bill/butsd.png'}
            alt=''
            onClick={() => this.buttClickd()}
          />
          <HR></HR> 
        <POLR>
          <Crew />
        </POLR>      
   	 </div>
		)
	}
}
const mapStateToProps = (state) => ({
  info: state.post.info,
  butsc: state.post.butsc,
  butsg: state.post.butsg
})
export default connect(mapStateToProps)(Card)